.footer {
  position: relative;
  padding-top: 113px;
  padding-bottom: 25px;
  background-color: $background-color;
  justify-content: center;

  @media screen and (min-width: $tablet) and (max-width: $max-tablet) {
    padding-top: 158px;
    padding-bottom: 18px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 165px;
    padding-bottom: 10px;
  }
}

.footer-line::after {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  height: 1px;
  background-color: $lines-color;
  bottom: 73px;

  @media screen and (min-width: $desktop) {
    bottom: 45px;
  }
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  vertical-align: baseline;
  justify-content: space-between;
  margin-bottom: 98px;
  text-align: end;
  align-items: baseline;
}

.footer-title {
  margin-bottom: 43px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 0;
  }
}

.label-title.label-footer,
.section-title.title-footer {
  display: block;
  margin-bottom: 15px;
  width: 234px;
  text-align: start;

  @media screen and (min-width: $tablet) {
    width: 340px;
    margin-bottom: 16px;
  }
  @media screen and (min-width: $desktop) {
    width: 500px;
    margin-bottom: 17px;
  }
}

.footer-social {
  display: inline-flex;
  margin-bottom: 46px;

  @media screen and (min-width: $desktop) {
    margin-bottom: 50px;
  }
}

.footer-item {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 34px;
  height: 34px;
  color: $white-text-color;
}

.footer-item:not(:last-child) {
  margin-right: 25px;
}

.footer-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 34px;
  height: 34px;

  background-color: $secondary-title-color;
  border-radius: 50%;
  color: $white-text-color;
}

.footer-link:hover,
.footer-link:focus {
  background-color: $primary-title-color;
  border-radius: 50%;
}

.footer-icon-github {
  width: 20px;
  height: 20px;
  fill: currentColor;
}

.footer-icon-telegram {
  width: 20px;
  height: 16px;
  fill: currentColor;
}

.footer-icon-facebook {
  width: 9px;
  height: 20px;
  fill: currentColor;
}

.footer-contacts {
  padding: 0;
  margin-left: auto;
}
.footer-contact {
  display: flex;
  font-size: $main-fsize-mobile;
  font-family: $main-ffamily;
  font-weight: 700;
  line-height: 1.86;

  text-align: right;

  @media screen and (min-width: $tablet) and (max-width: $max-tablet) {
    font-size: $main-fsize-tablet;
  }

  @media screen and (min-width: $desktop) {
    font-size: $main-fsize-desktop;
  }
}

.footer-tel {
  color: $primary-title-color;
}

.footer-copyright {
  min-width: 150px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  font-family: $main-ffamily;
  font-style: normal;
  font-weight: 700;
  font-size: $main-fsize-tablet;
  line-height: 1.86;
  letter-spacing: 0.02em;

  color: $secondary-text-color;
}
